<template >
    <div id="userHome" >
        <div id="loginWindows" >
            <div id="loginHead" >
                <div id="loginTitle" >
                    我的作品
                </div >
                <div id="closeIco" @click="closelogin()" title="关闭" >
                    ×
                </div >
            </div >
            <div id="loginBody" >
                <table id="fileTable" >
                    <thead >
                    <tr id="tableHead" >
                        <td style="width: 10%" >作品ID</td >
                        <td >作品名</td >
                        <td >保存时间</td >
                        <td style="width: 15%" >文件大小</td >
                        <td style="width: 20%" >操作</td >
                    </tr >
                    </thead >
                    <tbody >
                    <tr v-for="(list,index) in fileList" >
                        <td style="width: 10%" >{{list.fid}}</td >
                        <td >{{list.filename}}</td >
                        <td >{{list.time}}</td >
                        <td style="width: 15%" >{{list.filesize}}KB</td >
                        <td style="width: 20%" >
                            <div class="fileAction" @click="loadFile(list.fid)" >加载</div >
                            <div class="fileAction" @click="downloadFile(list.fid)" >下载</div >
                            <div class="fileAction" @click="delFile(list.fid,index)" >删除</div >
                        </td >
                    </tr >
                    </tbody >
                </table >
            </div >
        </div >
    </div >
</template >

<script >

    import $ from 'jquery';
    import config from '../../assets/js/config'

    var Base64 = require('js-base64').Base64;

    export default {
        name: "userHome",
        data() {
            return {
                fileList: [{
                    "fid": null,
                    "uid": null,
                    "filename": null,
                    "filesize": null,
                    "time": null
                },]
            };
        },

        // 初始化完成
        mounted() {
            this.getFileList()
        },

        // 其他函数
        methods: {
            // 关闭登录界面
            closelogin(data) {
                this.$emit('window-hide', {
                    type: 'userHome',
                    data: data
                })
                ;
            },

            // 获取作品列表
            getFileList() {
                let that = this;
                $.ajax({
                    url: config.serviseHost + "pyonline/file/getFileList",
                    dataType: "json",
                    data: {
                        'session': sessionStorage.sessionId
                    },
                    success: function (data) {
                        if (data.code == 200) {
                            that.fileList = data.fileList;
                        } else if (data.code == 201) {
                            alert('登录过期,请重新登录');
                            that.closelogin({
                                'type': 'logout'
                            })
                        } else {
                            alert('获取失败');
                        }
                    },
                    fail: function (error) {
                        alert('网络错误,请重试')
                    }
                })
            },

            // 加载文件
            loadFile(fileId) {
                let that = this;
                $.ajax({
                    url: config.serviseHost + "pyonline/file/getFile",
                    dataType: "json",
                    data: {
                        'session': sessionStorage.sessionId,
                        'fid': fileId
                    },
                    success: function (data) {
                        if (data.code == 200) {
                            that.closelogin({
                                'type': 'loadFile',
                                'codeData': data.fileInfo
                            });
                        } else if (data.code == 201) {
                            alert('登录过期,请重新登录');
                            that.closelogin({
                                'type': 'logout'
                            })
                        } else {
                            alert('获取失败')
                        }
                    },
                    fail: function (error) {
                        alert('网络错误,请重试')
                    }
                })
            },

            // 删除文件
            delFile(fileId, index) {
                let that = this;
                $.ajax({
                    url: config.serviseHost + "pyonline/file/delFile",
                    dataType: "json",
                    data: {
                        'session': sessionStorage.sessionId,
                        'fid': fileId
                    },
                    success: function (data) {
                        if (data.code == 200) {
                            that.fileList.splice(index, 1);
                        } else if (data.code == 201) {
                            alert('登录过期,请重新登录');
                            that.closelogin({
                                'type': 'logout'
                            })
                        } else {
                            alert('删除失败')
                        }
                    },
                    fail: function (error) {
                        alert('网络错误,请重试')
                    }
                })
            },

            // 下载文件
            downloadFile(fileId) {
                let that = this;
                $.ajax({
                    url: config.serviseHost + "pyonline/file/downloadFile",
                    dataType: "json",
                    data: {
                        'session': sessionStorage.sessionId,
                        'fid': fileId
                    },
                    success: function (data) {
                        if (data.code == 200) {
                            window.open(data.url, '_blank');
                        } else if (data.code == 201) {
                            alert('登录过期,请重新登录');
                            that.closelogin({
                                'type': 'logout'
                            })
                        } else {
                            alert('请求错误');
                        }
                    },
                    fail: function (error) {
                        alert('网络错误,请重试');
                    }
                })
            }
        }
    };
</script >

