import Vue from 'vue';
import VueRouter from 'vue-router';

import Index from '../components/index';

import PC_Index from '../components/pc/index';
import PC_About from '../components/pc/about';
import PC_Login from '../components/pc/login';
import PC_Register from '../components/pc/register'
import PC_UserHome from '../components/pc/userHome'

Vue.use(VueRouter);


const routes = [
    // {
    //   path: '/',
    //   name: 'home',
    //   component: Home
    // },
    {
        path: '/',
        name: 'Index',
        component: Index
    }, {
        path: '/pc/index',
        name: 'IndexPC',
        component: PC_Index
    }, {
        path: '/pc/about',
        name: 'about',
        component: PC_About
    }, {
        path: '/pc/login',
        name: 'login',
        component: PC_Login
    }, {
        path: '/pc/register',
        name: 'register',
        component: PC_Register
    }, {
        path: '/pc/userHome',
        name: 'userHome',
        component: PC_UserHome
    }
];

const router = new VueRouter({
    routes
});

export default router
