<template >
    <div id="index-pc" >
        <div class="toolbar" >
            <ul id="toolbarLeft" >
                <li class="home-ico" >
                    <a href="https://5ixf.vip" title="个人主页" target="_Blank" >
                        <svg t="1580623041639" class="icon" viewBox="0 0 1024 1024" version="1.1"
                             xmlns="http://www.w3.org/2000/svg" p-id="2613" width="24" height="40" >
                            <path d="M886.976 387.904l-362.666667-256a21.354667 21.354667 0 0 0-24.618666 0l-362.666667 256A21.376 21.376 0 0 0 128 405.333333v469.333334a21.333333 21.333333 0 0 0 21.333333 21.333333h234.666667a21.333333 21.333333 0 0 0 21.333333-21.333333V618.666667h213.333334v256a21.333333 21.333333 0 0 0 21.333333 21.333333h234.666667a21.333333 21.333333 0 0 0 21.333333-21.333333V405.333333a21.376 21.376 0 0 0-9.024-17.429333z"
                                  fill="#646464" p-id="2614" ></path >
                        </svg >
                    </a >
                </li >
                <li class="menuButton havaNextMenu" @mouseover="fileMenuHide=false"
                    @mouseleave="fileMenuHide=true" >
                    文件
                    <ul class="menuButtonNextMenu" v-if="!fileMenuHide" >
                        <li class="menuButtonNextMenuList" @click="newPython()" >新建</li >
                        <li class="menuButtonNextMenuList" >
                            <label for="chooseFile" >从本地打开</label ><input id="chooseFile" style="display: none"
                                                                          ref="inputFile"
                                                                          type="file"
                                                                          @change="chooseFile" >
                        </li >
                        <li class="menuButtonNextMenuList" @click="savePython()" >保存到电脑</li >
                    </ul >
                </li >
                <li class="menuButton" @click="uploadFile" >保存</li >
                <li class="inputFileName" ><input id="fileName" type="text" v-model="fileName" ></li >
                <li class="menuButton moreModel" >神秘功能开发中...</li >
            </ul >
            <ul id="toolbarRight" >
                <li class="menuButton" style="width: auto;max-width: 100px" :class="userInfo.uid>0?'havaNextMenu':''"
                    @click="login"
                    @mouseover="userMenuHide=false"
                    @mouseleave="userMenuHide=true" >
                    <div id="username" >{{userInfo.uid>0?userInfo.username:'登录'}}</div >
                    <ul class="menuButtonNextMenu" v-if="!userMenuHide&&userInfo.uid!=0" >
                        <li class="menuButtonNextMenuList" @click="userHome" >个人作品</li >
                        <li class="menuButtonNextMenuList" @click="logout" >退出登录</li >
                    </ul >
                </li >
                <li class="menuButton" @click="windowHide['about'] = !windowHide['about']" >
                    关于
                </li >
            </ul >
        </div >
        <div class="codeRegion" >
            <div class="pageLeft" >
                <div id="codeEditior" ref="codeEditor" ></div >
            </div >
            <div class="pageRight" >
                <div class="pageRightHead" >
                    <div id="runPython" @click="runPython()" >{{isRunPython?'停止':'运行'}}</div >
                    <div id="clearn" @click="clearnPython()" >清除</div >
                </div >
                <div class="pageRightBody" >
                    <div id="runOutPut" >{{returncode}}</div >
                </div >
                <div class="pageRightFoot" >
                    <div id="pyCanvas" ref="pyCanvas" ></div >
                </div >
            </div >
        </div >
        <PC_Login v-if="!windowHide['login']" @window-hide="hideWindow" ></PC_Login >
        <PC_About v-if="!windowHide['about']" @window-hide="hideWindow" ></PC_About >
        <PC_UserHome v-if="!windowHide['userHome']" @window-hide="hideWindow" ></PC_UserHome >
        <PC_Register v-if="!windowHide['register']" @window-hide="hideWindow" ></PC_Register >
    </div >
</template >


<script >
    import $ from 'jquery';

    let Base64 = require('js-base64').Base64;

    import config from '../../assets/js/config'

    // require('../../assets/js/python.min');
    // require('../../assets/js/python-stdlib');

    require('../../assets/js/v1.1.0-20200227/python.min.js')
    require('../../assets/js/v1.1.0-20200227/python-stdlib.js')

    import * as monaco from 'monaco-editor';

    import PC_About from "./about";
    import PC_Login from './login';
    import PC_Register from './register';
    import PC_UserHome from './userHome';

    // python的代码提示功能
    // require('../assets/js/pythonTurtle');

    export default {
        name: "index-pc",
        components: {PC_Login, PC_Register, PC_About, PC_UserHome},
        data() {
            return {
                // 初始代码
                codes: 'import random\n' +
                    'import turtle\n' +
                    '\n' +
                    '# 分形樱花树\n' +
                    '\n' +
                    '# “画树”函数\n' +
                    '# 参数分别是树枝长度、画笔\n' +
                    'def tree(branchLen, t):\n' +
                    '    if (branchLen > 3):\n' +
                    '        if (8 <= branchLen and branchLen <= 12) :\n' +
                    '            if (random.randint(0, 2) == 0) :\n' +
                    '                t.pencolor(\'snow\')\n' +
                    '            else :\n' +
                    '                t.pencolor(\'lightcoral\')\n' +
                    '            t.pensize((branchLen / 3))\n' +
                    '        elif (branchLen < 8) :\n' +
                    '            if (random.randint(0, 1) == 0) :\n' +
                    '                t.pencolor(\'snow\')\n' +
                    '            else :\n' +
                    '                t.pencolor(\'lightcoral\')\n' +
                    '            t.pensize((branchLen / 2))\n' +
                    '        else :\n' +
                    '            t.pencolor(\'sienna\')\n' +
                    '            t.pensize((branchLen / 10))\n' +
                    '        t.forward(branchLen)\n' +
                    '        a = (1.5 * (random.random()))\n' +
                    '        t.right((20 * a))\n' +
                    '        b = (1.5 * (random.random()))\n' +
                    '        # 在tree函数内部调自己，这种方式叫递归调用\n' +
                    '        # 递归可以大幅提升编程效率\n' +
                    '        tree(branchLen - 10 * b, t)\n' +
                    '        t.left((40 * a))\n' +
                    '        tree(branchLen - 10 * b, t)\n' +
                    '        t.right((20 * a))\n' +
                    '        t.penup()\n' +
                    '        t.backward(branchLen)\n' +
                    '        t.pendown()\n' +
                    '\n' +
                    '# 绘制树下花瓣，参数分别是画板数、画笔\n' +
                    'def petal(m, t):\n' +
                    '    for i in range(m):\n' +
                    '        a = (200 - 400 * (random.random()))\n' +
                    '        b = (10 - 20 * (random.random()))\n' +
                    '        t.penup()\n' +
                    '        t.forward(b)\n' +
                    '        t.left(90)\n' +
                    '        t.forward(a)\n' +
                    '        t.pendown()\n' +
                    '        t.pencolor(\'lightcoral\')\n' +
                    '        t.circle(1)\n' +
                    '        t.penup()\n' +
                    '        t.backward(a)\n' +
                    '        t.right(90)\n' +
                    '        t.backward(b)\n' +
                    '\n' +
                    '# 主函数\n' +
                    'def main():\n' +
                    '    t = turtle.Pen()\n' +
                    '    t.hideturtle()\n' +
                    '    t.speed(0)\n' +
                    '    turtle.bgcolor(\'wheat\')\n' +
                    '    t.left(90)\n' +
                    '    t.penup()\n' +
                    '    t.backward(150)\n' +
                    '    t.pendown()\n' +
                    '    t.pencolor(\'sienna\')\n' +
                    '    tree(60, t)\n' +
                    '    petal(100, t)\n' +
                    '    turtle.done()\n' +
                    '\n' +
                    '# 程序入口\n' +
                    'main()\n',
                // 运行输出
                returncode: '',
                // 编辑器
                monacoEditor: null,
                // 编辑器状态
                isRunPython: false,
                // 文件名称
                fileName: '未命名作品',
                // 菜单隐藏
                fileMenuHide: true,
                // 菜单隐藏
                windowHide: {
                    // 关于菜单隐藏
                    about: false,
                    // 登录界面隐藏
                    login: true,
                    // 用户主页隐藏
                    userHome: true,
                    // 注册页面
                    register: true,
                },
                // 用户信息
                userInfo: {"uid": 0, "username": null, "tel": null, "email": null},
                // 个人中心菜单隐藏
                userMenuHide: true,
            }
        },

        // 初始化完成
        mounted() {
            let that = this;
            // 开启退出提醒
            that.ifclose();
            // 配置编辑器
            that.configMonacoEditor();
            // 更新用户信息
            that.updateUserInfo();
        },

        // 其他函数
        methods: {
            // 运行python代码
            runPython() {
                let that = this;

                // 调整显示状态
                that.isRunPython = !that.isRunPython;

                if (that.isRunPython) {
                    // 运行程序
                    that.doRunPython(that.monacoEditor.getValue());
                } else {
                    //停止程序
                    that.doRunPython("import turtle\np = turtle.Pen()");
                }
            },

            // 新建作品
            newPython() {
                let that = this;
                //利用对话框返回的值 （true 或者 false）
                if (confirm("你确定清空当前作品么？")) {
                    that.monacoEditor.setValue('');
                }
            },

            exportRaw(name, data) {
                var urlObject = window.URL || window.webkitURL || window;
                var export_blob = new Blob([data]);
                var save_link = document.createElementNS("http://www.w3.org/1999/xhtml", "a")
                save_link.href = urlObject.createObjectURL(export_blob);
                save_link.download = name;
                this.fakeClick(save_link);
            },

            fakeClick(obj) {
                var ev = document.createEvent("MouseEvents");
                ev.initMouseEvent("click", true, false, window, 0, 0, 0, 0, 0, false, false, false, false, 0, null);
                obj.dispatchEvent(ev);
            },

            // 保存文件
            savePython() {
                let that = this;
                if (that.monacoEditor.getValue() != "") {
                    this.exportRaw(that.fileName == '' ? '未命名作品' : that.fileName + '.py', that.monacoEditor.getValue());
                }
            },

            // 清空输出
            clearnPython() {
                let that = this;
                that.doRunPython("import turtle\np = turtle.Pen()");
            },

            // 检测页面关闭
            ifclose() {
                // 关闭提醒
                window.onbeforeunload = function (event) {
                    return '您确定要关闭网页嘛?';
                };
            },

            // 配置编辑器
            configMonacoEditor() {
                let that = this;
                that.monacoEditor = monaco.editor.create(that.$refs.codeEditor, {
                        //编辑器内容
                        value: that.codes,
                        //语言
                        language: 'python',
                        //编辑器主题：vs, hc-black, or vs-dark
                        theme: 'vs-dark',
                        // 编辑器配置
                        editorOptions: function (e) {
                            return {
                                // 单击行号选中对应行号
                                selectOnLineNumbers: true,
                                // 使用制表符停止
                                useTabStops: true,
                                // 自动代码格式化
                                formatOnPaste: true,
                                autoIndent: true,
                                formatOnType: true,
                                // 自动提示
                                quickSuggestions: true,
                            }
                        }
                    }
                );

                // 快捷键保存文件到本地
                that.monacoEditor.addCommand(monaco.KeyMod.CtrlCmd | monaco.KeyCode.KEY_S, function () {
                    that.savePython();
                });

            },

            // 运行python
            doRunPython(code) {
                let that = this;
                //清空输出内容
                that.returncode = '';
                var pyCanvas = that.$refs.pyCanvas;
                Sk.configure({
                    output: function (text) {
                        that.returncode += text;
                    },
                    read: function (x) {
                        if (Sk.builtinFiles === undefined || Sk.builtinFiles["files"][x] === undefined)
                            throw "File not found: '" + x + "'";
                        return Sk.builtinFiles["files"][x];
                    },
                    __future__: Sk.python3
                });
                Sk.TurtleGraphics = {
                    'target': 'pyCanvas',
                    'width': pyCanvas.offsetWidth,
                    'height': pyCanvas.offsetHeight
                };
                // 开始执行代码
                var myPromise = Sk.misceval.asyncToPromise(function () {
                    return Sk.importMainWithBody("<stdin>", false, code, true);
                });
                // 执行结束输出执行结果
                myPromise.then(function (mod) {
                        that.isRunPython = false;
                    },
                    function (err) {
                        that.isRunPython = false;
                        that.returncode = err.toString();
                        console.log(err.toString());
                    }
                );
            },

            // 登录或个人中心
            userHome() {
                let that = this;
                that.windowHide['userHome'] = !that.windowHide['userHome'];
            },

            login() {
                let that = this;
                if (that.userInfo.uid == 0) {
                    that.windowHide['login'] = !that.windowHide['login'];
                }
            },

            // 关闭弹窗
            hideWindow(data) {
                let that = this;
                that.windowHide[data.type] = true;
                switch (data.type) {
                    case 'login':
                        // 登录
                        if (data.data != undefined) {
                            if (data.data.type == "register") {
                                // 打开注册页面
                                that.windowHide['register'] = false;
                            } else if (data.data.type == "longinOK") {
                                // 登录成功
                                that.userInfo = data.data.userInfo
                            }
                        }
                        break;
                    case 'register':
                        // 注册
                        if (data.data != undefined) {
                            that.userInfo = data.data;
                        }
                        break;
                    case 'userHome':
                        if (data.data != undefined) {
                            if (data.data.type == 'logout') {
                                // 登录失效
                                that.logout();
                            } else if (data.data.type == 'loadFile') {
                                // 加载云端代码
                                that.monacoEditor.setValue(Base64.decode(data.data.fileData).replace("\n", "\r\n"));
                                that.fileName = data.data.fileName;
                            }
                        }
                        break;
                }
            },

            // 更新用户信息
            updateUserInfo() {
                let that = this;
                if (sessionStorage.userInfo != '' && sessionStorage.userInfo != null) {
                    that.userInfo = JSON.parse(sessionStorage.userInfo);
                }
            },

            // 退出登录
            logout() {
                let that = this;
                sessionStorage.clear();
                that.userInfo = {"uid": 0, "username": null, "tel": null, "email": null};
                // 服务器提交退出登录
            },

            // 保存文件到云端
            uploadFile() {
                let that = this;
                if (sessionStorage.userInfo != '' && sessionStorage.userInfo != null) {
                    $.ajax({
                        url: config.serviseHost + 'pyonline/file/uploadFile',
                        type: 'post',
                        dataType: "json",
                        data: {
                            'type': 'data',
                            'filename': that.fileName == "未命名作品" ? that.fileName + new Date().getTime() + '.py' : that.fileName,
                            'filedata': Base64.encode(that.monacoEditor.getValue()),
                            'session': sessionStorage.sessionId
                        },
                        success: function (data) {
                            if (data.code == 200) {
                                alert('保存成功');
                            } else if (data.code == 201) {
                                alert("登录过期,请重新登录");
                                that.logout();
                            } else {
                                alert('提交失败')
                            }
                        },
                        fail: function (error) {
                            alert('网络请求失败')
                        }
                    })
                } else {
                    that.windowHide['login'] = !that.windowHide['login'];
                }
            },

            // 本地上传文件
            chooseFile(e) {
                let that = this;
                const file = e.target.files[0];
                const reader = new FileReader();
                reader.onload = e => that.$emit("load", that.dealNum(e.target.result, file));
                reader.readAsText(file);
            },

            // 读取问点内容
            dealNum(item, file) {
                let that = this;
                if (file.size / 1024 <= 200) {
                    that.monacoEditor.setValue(item);
                    that.fileName = file.name;
                } else {
                    alert('文件过大,请上传200k以内的文件' + file.size / 1024);
                }
            }
        }
    };
</script >

