<template>
    <div id="login">
        <div id="loginWindows">
            <div id="loginHead">
                <div id="loginTitle">
                    登录
                </div>
                <div id="closeIco" @click="closeLogin()" title="关闭">
                    ×
                </div>
            </div>
            <div id="loginBody">
                <div id="loginInfo">
                    <div class="inputList">
                        <span></span>
                        <input type="text" v-model="userInfo['username']" placeholder="UID/用户名/邮箱/手机号">
                    </div>
                    <div class="inputList">
                        <span></span>
                        <input type="text" v-model="userInfo['password']" placeholder="密码">
                    </div>
                    <div class="inputList">
                        <div class="divButton" @click="login">登录</div>
                    </div>
                    <hr id="splitLine">
                    <div class="inputList registered">
                        <div class="divButton" id="registered" @click="register">注册</div>
                    </div>
                </div>
            
            </div>
        </div>
    </div>
</template>

<script>

    import $ from 'jquery';
    import config from '../../assets/js/config'

    let md5 = require('md5');

    export default {
        name: "login",
        data() {
            return {
                userInfo: {
                    'username': '',
                    'password': ''
                }
            };
        },

        // 初始化完成
        mounted() {

        },

        // 其他函数
        methods: {
            // 关闭登录界面
            closeLogin(data) {
                this.$emit('window-hide', {
                    type: 'login',
                    data: data
                });
            },

            // 登录
            login() {
                let that = this;
                if (that.userInfo['username'] != '' && that.userInfo['password'] != '') {

                    $.ajax({
                        type: 'post',
                        url: config.serviseHost + 'pyonline/user/login',
                        dataType: "json",
                        data: {
                            'username': that.userInfo['username'],
                            'password': md5(that.userInfo['password'])
                        },
                        success: function (data) {
                            if (data.code == 200) {
                                alert('登录成功');
                                that.saveData2Session(data);
                                that.closeLogin({
                                    'type': 'longinOK',
                                    'userInfo': data.userInfo
                                });
                            } else {
                                alert('登录失败,请重试')
                            }
                        },
                        fail: function (error) {
                            alert('网络错误,请重试')
                        }
                    })
                } else {
                    alert('请输入用户名和密码');
                }
            },

            // 注册功能
            register() {
                let that = this;
                that.closeLogin({
                    'type': 'register',
                })
            },

            // 保存数据到session
            saveData2Session(data) {
                sessionStorage.sessionId = data.session;
                sessionStorage.userInfo = JSON.stringify(data.userInfo);
            }
        }
    };
</script>

