<template >
    <div id="register" >
        <div id="registerWindows" >
            <div id="registerHead" >
                <div id="registerTitle" >
                    注册
                </div >
                <div id="closeIco" @click="closeregister()" title="关闭" >
                    ×
                </div >
            </div >
            <div id="registerBody" >
                <div id="registerInfo" >
                    <div class="inputList" >
                        <span ></span >
                        <input type="text" v-model="userInfo['username']" placeholder="用户名(必填)" >
                    </div >
                    <div class="inputList" >
                        <span ></span >
                        <input type="email" v-model="userInfo['email']" placeholder="邮箱" >
                    </div >
                    <div class="inputList" >
                        <span ></span >
                        <input type="text" v-model="userInfo['tel']" placeholder="手机号" >
                    </div >
                    <div class="inputList" >
                        <span ></span >
                        <input type="text" v-model="userInfo['password']" placeholder="密码(必填)" >
                    </div >
                    <div class="inputList" >
                        <span ></span >
                        <input type="text" v-model="userInfo['password2']" placeholder="二次确认密码(必填)" >
                    </div >
                    <hr id="splitLine" >
                    <div class="inputList registered" >
                        <div class="divButton" id="registered" @click="register" >注册</div >
                    </div >
                </div >
            
            </div >
        </div >
    </div >
</template >

<script >

    import $ from 'jquery';
    import config from '../../assets/js/config'

    let md5 = require('md5');

    export default {
        name: "register",
        data() {
            return {
                userInfo: {
                    'username': '',
                    'tel': '',
                    "email": '',
                    'password': '',
                    "password2": ''
                }
            };
        },

        // 初始化完成
        mounted() {

        },

        // 其他函数
        methods: {
            // 关闭注册界面
            closeregister(data) {
                this.$emit('window-hide', {
                    type: 'register',
                    data: data
                });
            },

            // 注册功能
            register() {
                let that = this;
                if (that.userInfo['username'] != '' && that.userInfo['password'] != '' && that.userInfo['password'] == that.userInfo['password2']) {
                    $.ajax({
                        type: 'post',
                        url: config.serviseHost + 'pyonline/user/register',
                        dataType: "json",
                        data: {
                            'username': that.userInfo['username'],
                            'password': md5(that.userInfo['password']),
                            'email': that.userInfo['email'],
                            'tel': that.userInfo['tel'],
                        },
                        success: function (data) {
                            if (data.code == 200) {
                                alert('注册成功');
                                that.saveData2Session(data);
                                that.closeregister(data.userInfo);
                            } else if (data.code == 202) {
                                alert(data.msg);
                            } else {
                                alert('注册失败,请重试')
                            }
                        },
                        fail: function (error) {
                            alert('网络请求失败')
                        }
                    })
                } else {
                    alert('请检查输入项');
                }
            },

            // 保存数据到session
            saveData2Session(data) {
                sessionStorage.sessionId = data.session;
                sessionStorage.userInfo = JSON.stringify(data.userInfo);
            }
        }
    };
</script >

